export default {
	data() {
		return {
			productList: [],
		};
	},
	onLoad() {
		this._load()
	},
	methods:{
		_load() {
			this.$api.http.get('product/get_recent',{type:'hot'}).then(res=>{    //热门
				this.productList = res.data
			})
		}
	}
}
